import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			// cartList: [{
			// 	      car_id: 1,//购物车列表id
			// 	        goods_id:100,//商品id
			// 	        attr_val: "春装款 L",//商品规格 = 二级规格值+'空格 '+二级规格值
			// 	        image: "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1574139364727&di=96355913d56cf3f09683a6ee4a3781ce&imgtype=0&src=http%3A%2F%2Fimg0.pconline.com.cn%2Fpconline%2F1702%2F12%2F8823860_13_thumb.jpg",//商品照片
			// 	        number: 1, //商品数量
			// 	        price: 278 , //商品规格对应的商品价格
			// 	        stock: 15, //商品库存
			// 			disRate:0.5,//折扣比率
			// 	        title: "OVBE 长袖风衣",//商品名称
			// 			specList:[{id:1,pid:2},{id:5,pid:8}],
			// },
			// {
			// 	      id: 54,//购物车列表id
			// 	        goods_id:855,//商品id
			// 	        attr_val: "xs 白色",
			// 	        image: "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2758194880,3262364285&fm=26&gp=0.jpg",//商品照片
			// 	        number: 7, //商品数量
			// 	        price: 30 , //商品规格对应的商品价格
			// 	        stock: 10, //商品库存
			// 			disRate:0.5,//折扣比率
			// 	        title: "OVBE 长袖风衣",//商品名称
			// 			specList:[{id:22,pid:7},{id:15,pid:10}],
			// },],
		};
	},
	onLoad() {
		this.loadData();
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
			console.log(e.length, this.empty, 'e.length ')
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData() {
			// let list = await this.$api.json('cartList'); 
			const res = await this.$util.get('/shop/getGoodsCar')

			let cartList = res.data
			cartList = cartList.map(item => {
				item.checked = true;
				return item;
			});
			this.cartList = cartList;
			console.log(cartList, 'cartList')
			this.calcTotal(); //计算总价

		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			console.log(data);
			this.cartList[data.index].num = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row._id;

			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.num;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder() {
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item => {
				if (item.checked) {
					item.spec[0].num = item.num
					goodsData.push(item)
					console.log(goodsData, 'goodsData')
				}
			})
			if(goodsData.length==0){
				return this.$util.fail('请先勾选商品')
			}
			//调起生成订单的接口,然生成订单id,传goodsData参数
			uni.setStorageSync('selectedGoods',goodsData)
			uni.navigateTo({
				url: `/pages/shopMall/confimOrder/confimOrder`
			})
			// this.$api.msg('跳转下一页 sendData');
		}
	}
}
